.hero {
  padding-top: calc(134px + 43px);

  @include medium-tablet {
    padding-top: calc(110px + 20px);
  }

  @media (max-width: 860px) {
    padding-top: calc(134px + 20px);
  }

  @include mobile {
    padding-top: calc(96px + 20px);
  }

  &__container {
    position: relative;
    margin-bottom: 60px;
    display: flex;
    padding: 105px 0 105px 105px;
    border-radius: 10px;
    background-color: var(--lightBlue-color);

    @include desktop {
      padding: 105px 0 105px 60px;
    }

    @include tablet {
      padding: 60px 0 60px 40px;
    }

    @include medium-tablet {
      padding: 50px 40px 50px 40px;
    }

    @include small-tablet {
      padding: 50px 40px 500px 40px;
    }

    @include mobile {
      padding: 25px 20px 510px 20px;
    }
  }

  &__content {
    max-width: 488px;
    z-index: 10;

    @include medium-tablet {
      max-width: 420px;
    }

    @include small-tablet {
      max-width: 100%;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 34px;
    font-weight: 700;
    font-size: 72px;
    line-height: 114.5%;
    color: var(--dark-color);

    span {
      color: var(--primary-color);
    }

    @include tablet {
      font-size: 64px;
    }

    @include small-tablet {
      font-size: 48px;
    }

    @include mobile {
      margin-bottom: 25px;
      font-size: 34px;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 43px;
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    color: var(--dark-color);

    span {
      font-weight: 700;
    }

    @include medium-tablet {
      max-width: 420px;
    }

    @include mobile {
      font-size: 16px;
      margin-bottom: 20px;
    }
  }

  &__image {
    position: absolute;
    top: -43px;
    right: 10px;

    @include tablet {
      width: 390px;
      height: 563px;
      top: -52px;
    }

    @include medium-tablet {
      top: -8px;
    }

    @media (max-width: 860px) {
      width: 320px;
      height: 460px;
      top: 92px;
      right: 0;
    }

    @include small-tablet {
      top: 425px;
      right: 100px;
    }

    @include mobile {
      top: 400px;
      right: 0px;
    }
  }
}

.form {
  display: flex;
  align-items: center;
  max-width: 100%;

  &__connect {
    max-width: 488px;
  }

  &__label {
    margin-right: 20px;

    @include medium-tablet {
      margin-right: 0;
      margin-bottom: 20px;
    }

    @include small-tablet {
      margin-right: 20px;
      margin-bottom: 0;
    }

    @include mobile {
      margin-right: 0;
      margin-bottom: 10px;
    }
  }

  &__input {
    padding: 12px 0 12px 16px;
    max-width: 100%;
    font-weight: 400;
    font-size: 16px;
    line-height: 114.5%;
    color: var(--grey-color);
    background: #FFFFFF;
    box-shadow: 0px 4px 87px #E3E3E3;
    border-radius: 5px;

    @include small-tablet {
      max-width: 500px;
    }
  }

  &__btn {
    padding: 12px 42px 12px 42px;
    font-weight: 400;
    font-size: 16px;
    line-height: 114.5%;
    color: #FFFFFF;
    background: #2864FF;
    box-shadow: 0px 4px 8px #B0C6FF;
  }

  @include medium-tablet {
    flex-direction: column;
    align-items: flex-start;
  }

  @include small-tablet {
    flex-direction: row;
    align-items: center;
  }

@include mobile {
  flex-direction: column;
  align-items: flex-start;
}
}
