.header {
  position: absolute;
  left: 0;
  top: 0;
  padding: 34px 0 46px 0;
  width: 100%;

  @include tablet {
    padding: 28px 0 34px 0;
  }

  @include medium-tablet {
    padding: 24px 0 28px 0;
  }

  @include mobile {
    padding: 18px 0 20px 0;
  }



  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

}

.nav {
  &__list {
    display: flex;
    align-items: center;

    @include small-tablet {
      @include flex-all-center;
      flex-direction: column;
      width: 100%;
      height: 100%;
    }
  }

  &__item:not(:last-child) {
    margin-right: 45px;

    @include small-tablet {
      margin-right: 0;
      margin-bottom: 50px;
    }
  }

  &__link {
    font-weight: 400;
    font-size: 18px;
    line-height: 23px;
    color: var(--blue-color);
    transition: color 0.3s ease-in-out;

    @include small-tablet {
      font-weight: 700;
      font-size: 24px;
      line-height: 28px;
    }

    &:hover {
      color: var(--secondary-color);
    }
  }

  @include small-tablet {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 500;
    overflow-y: auto;
    width: 100%;
    height: 100vh;
    background-color: var(--light-color);
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }
}

.menu--active {
  opacity: 1;
  visibility: visible;
}

.social {
  display: flex;
  align-items: center;

  &__item:not(:last-child) {
    margin-right: 20px;
  }

  &__link {
    display: block;
    width: 40px;
    height: 40px;

    svg {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    circle {
      opacity: 0;
      transition: opacity 0.3s ease-in;
    }

    &:hover {
      circle {
        opacity: 0.7;
      }
    }
  }
}

.burger {
  @include burger;

  @media (min-width: 576px) {
    display: none;
  }
}
