:root {
  --font-family: "DM Sans", sans-serif;
  --content-width: 1200px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --primary-color: #2864FF;
  --blue-color: #002480;
  --secondary-color: #FFC228;
  --dark-color: #323232;
  --grey-color: #787878;
  --lightBlue-color: rgba(212, 224, 255, 0.35);
  --light-color: #fff;
}

